@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=Black+Ops+One&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Audiowide", cursive;width:100%;background:#252425}.pacman-loader{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100%;height:100vh}.game-logo{color:#f44336;margin:1.5rem;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;cursor:pointer}.game-logo span{font-size:4rem;padding:1.5rem}#home-page-titles{font-size:4rem;font-family:"Audiowide", cursive;font-weight:lighter;color:#f44336;margin-top:5rem;margin-bottom:1.7rem;letter-spacing:2px}#card-title{color:#db0505;font-family:"Audiowide", cursive;letter-spacing:1.5px}#card-release-date{color:#333;font-size:1.2rem}#card{transition:-webkit-transform 0.5s;transition:transform 0.5s;transition:transform 0.5s, -webkit-transform 0.5s}#card image{object-fit:cover}#card:hover{-webkit-transform:scale(0.98);transform:scale(0.98)}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{background:#000000;border-radius:5px}::-webkit-scrollbar-thumb{background:#f44336;border-radius:5px}::-webkit-scrollbar-thumb:hover{background:#ee5e5e;border-radius:5px}@media (max-width: 669px){#home-page-titles{font-size:3.5rem}}@media (max-width: 579px){.game-logo span{font-size:3.14rem}#home-page-titles{font-size:3rem}}@media (max-width: 505px){.game-logo span{font-size:3rem}#home-page-titles{font-size:2.8rem}#card{width:95vw}}@media (max-width: 477px){.game-logo span{font-size:2.5rem}#home-page-titles{font-size:2.2rem}#card-media{width:100%}}@media (max-width: 427px){.game-logo span{font-size:2.02rem}#home-page-titles{font-size:2.03rem}}@media (max-width: 385px){.game-logo #gamepad-logo{font:10rem}.game-logo span{font-size:1.9rem}#home-page-titles{font-size:1.8rem}}@media (max-width: 367px){.game-logo span{font-size:1.47rem}}.search-section{margin-top:3rem;margin-bottom:3rem}.search-section #search-bar{background:#000;color:white;text-align:center;font-weight:bold;font-size:1.67rem;letter-spacing:2.5px;padding:0.7rem;box-shadow:0px 0px 8px #f44336;width:40vw;border-radius:8px}.search-section #search-btn{height:2.88rem;border-radius:8px;margin-left:0.4rem;padding:1.4rem;box-shadow:0px 0px 3px #f44336}@media (max-width: 768px){.search-section #search-bar{font-size:1.25rem;width:80vw;height:1.4rem}.search-section #search-btn{width:15vw}.search-section .btn-div{width:100vw;padding:1rem}}@media (max-width: 420px){.search-section .btn-div{width:100vw;padding:1rem}.search-section #search-btn{width:15vw}}#detail-modal{overflow-x:hidden}#detail-modal .close-modal{display:-webkit-flex;display:flex;width:100%;-webkit-justify-content:flex-end;justify-content:flex-end}#detail-modal #close-icon{color:#f44336;height:2.5rem;width:2.5rem;margin:0.08rem}#detail-modal #close-icon:hover{background:#db0505;color:white;border-radius:13px}#detail-modal h2{font-size:2.5rem;font-family:"Audiowide", cursive;font-weight:lighter;color:#db0505;margin-top:3rem;margin-bottom:1rem;letter-spacing:2px}#detail-modal .star-rating{color:rgba(247,31,3,0.932);margin-bottom:1rem}#detail-modal h4{margin-top:0.25rem;margin-bottom:0.5rem}#detail-modal p{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;margin-top:2rem;margin-bottom:2rem}#detail-modal .description-gallery{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;margin-top:1rem;-webkit-flex-wrap:wrap;flex-wrap:wrap}#detail-modal .description-gallery img{height:70%;width:70%;margin:1rem;border-radius:3px}@media (max-width: 816px){#detail-modal .description-gallery img{height:100%;width:100%}}

